@layer base {
  * {
    border-color: var(--border);
    outline-color: var(--ring);
    @supports (color: color-mix(in lab, red, red)) {
      outline-color: color-mix(in oklab, var(--ring) 50%, transparent);
    }
  }
  body {
    background-color: var(--background);
    color: var(--foreground);
  }
}
:root {
  --bg-1: #f1f5f9;
  --bg-2: #dbeafe;
  --bg-3: #60a5fa;
  --glow: rgba(56, 189, 248, 0.25);
}
.weather-bg {
  background: radial-gradient(
      1200px 900px at 20% 10%,
      var(--bg-3),
      transparent 60%
    ),
    radial-gradient(
      900px 700px at 80% 30%,
      var(--bg-2),
      transparent 55%
    ),
    linear-gradient(
      180deg,
      var(--bg-1),
      var(--bg-2)
    );
}
.mc-sun {
  width: 180px;
  height: 180px;
  border-radius: 9999px;
  background: radial-gradient(circle at 30% 30%, rgba(255,255,255,0.95), rgba(255,226,115,0.95) 35%, rgba(255,163,26,0.85) 70%, rgba(255,132,0,0.4) 100%);
  box-shadow: 0 0 80px rgba(255, 193, 7, 0.35),
    0 0 140px rgba(251, 191, 36, 0.25);
  animation: mc-float 7s ease-in-out infinite;
}
.mc-moon {
  width: 170px;
  height: 170px;
  border-radius: 9999px;
  background: radial-gradient(circle at 30% 25%, rgba(255,255,255,0.95), rgba(226,232,240,0.92) 55%, rgba(148,163,184,0.65) 100%);
  box-shadow: 0 0 90px rgba(203, 213, 225, 0.25),
    0 0 140px rgba(167, 139, 250, 0.18);
  position: relative;
  animation: mc-float 8.5s ease-in-out infinite;
}
.mc-moon::after {
  content: "";
  position: absolute;
  inset: 0;
  border-radius: 9999px;
  background: radial-gradient(circle at 35% 45%, rgba(148,163,184,0.28) 0 10px, transparent 11px),
    radial-gradient(circle at 55% 30%, rgba(148,163,184,0.22) 0 7px, transparent 8px),
    radial-gradient(circle at 60% 60%, rgba(148,163,184,0.18) 0 12px, transparent 13px);
  mix-blend-mode: multiply;
  opacity: 0.6;
}
@keyframes mc-float {
  0%, 100% {
    transform: translate(0, 0);
  }
  50% {
    transform: translate(-10px, 10px);
  }
}
.mc-sunrays {
  position: absolute;
  inset: -40%;
  background: conic-gradient(from 220deg at 100% 0%,
      rgba(255, 214, 102, 0.00) 0deg,
      rgba(255, 214, 102, 0.30) 14deg,
      rgba(255, 214, 102, 0.00) 28deg,
      rgba(255, 214, 102, 0.22) 46deg,
      rgba(255, 214, 102, 0.00) 60deg,
      rgba(255, 214, 102, 0.18) 86deg,
      rgba(255, 214, 102, 0.00) 100deg
    );
  mask-image: radial-gradient(closest-side at 100% 0%, rgba(0,0,0,1), rgba(0,0,0,0) 70%);
  -webkit-mask-image: radial-gradient(closest-side at 100% 0%, rgba(0,0,0,1), rgba(0,0,0,0) 70%);
  mix-blend-mode: screen;
  opacity: 0.75;
  animation: mc-rays 18s linear infinite;
}
@keyframes mc-rays {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.mc-cloud {
  position: absolute;
  border-radius: 9999px;
  filter: blur(42px);
  opacity: 0.7;
  animation: mc-cloud-drift 38s linear infinite;
}
[data-variant="light"] .mc-cloud {
  background: rgba(15, 23, 42, 0.12);
  opacity: 0.55;
}
[data-variant="dark"] .mc-cloud {
  background: rgba(2, 6, 23, 0.22);
  opacity: 0.75;
}
.mc-cloud--1 {
  width: 520px;
  height: 240px;
  top: 8%;
  left: -30%;
  animation-duration: 42s;
}
.mc-cloud--2 {
  width: 620px;
  height: 300px;
  top: 18%;
  left: -40%;
  animation-duration: 55s;
}
.mc-cloud--3 {
  width: 480px;
  height: 220px;
  top: 32%;
  left: -35%;
  animation-duration: 46s;
}
.mc-cloud--4 {
  width: 760px;
  height: 340px;
  top: 46%;
  left: -55%;
  animation-duration: 62s;
}
.mc-cloud--5 {
  width: 560px;
  height: 250px;
  top: 60%;
  left: -45%;
  animation-duration: 52s;
}
@keyframes mc-cloud-drift {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(180%);
  }
}
.mc-fog {
  position: absolute;
  inset: -20% -30% -20% -30%;
  background: radial-gradient(closest-side at 30% 40%, rgba(255,255,255,0.35), rgba(255,255,255,0) 70%),
    radial-gradient(closest-side at 70% 50%, rgba(255,255,255,0.25), rgba(255,255,255,0) 75%),
    linear-gradient(180deg, rgba(255,255,255,0.18), rgba(255,255,255,0.05));
  filter: blur(18px);
  opacity: 0.75;
  mix-blend-mode: overlay;
}
.mc-fog--1 {
  animation: mc-fog-a 26s ease-in-out infinite;
}
.mc-fog--2 {
  animation: mc-fog-b 34s ease-in-out infinite;
  opacity: 0.6;
}
@keyframes mc-fog-a {
  0% {
    transform: translate(-6%, -6%);
  }
  50% {
    transform: translate(6%, 6%);
  }
  100% {
    transform: translate(-6%, -6%);
  }
}
@keyframes mc-fog-b {
  0% {
    transform: translate(8%, -10%);
  }
  50% {
    transform: translate(-8%, 10%);
  }
  100% {
    transform: translate(8%, -10%);
  }
}
.mc-heat {
  position: absolute;
  inset: -10% -20% -20% -20%;
  background: repeating-linear-gradient(
      0deg,
      rgba(255, 102, 0, 0.00) 0px,
      rgba(255, 102, 0, 0.10) 10px,
      rgba(255, 102, 0, 0.00) 20px
    );
  opacity: 0.55;
  filter: blur(0.8px);
  transform: translateY(30%);
  animation: mc-heat-rise 6.5s ease-in-out infinite;
  mix-blend-mode: overlay;
}
.mc-heat--2 {
  opacity: 0.35;
  filter: blur(1.2px);
  animation-duration: 8.5s;
}
@keyframes mc-heat-rise {
  0% {
    transform: translateY(35%);
  }
  50% {
    transform: translateY(18%);
  }
  100% {
    transform: translateY(35%);
  }
}
.rain-layer {
  position: absolute;
  inset: -20%;
  background-image: repeating-linear-gradient(
    120deg,
    rgba(255,255,255,0.14) 0px,
    rgba(255,255,255,0.14) 1px,
    transparent 1px,
    transparent 12px
  );
  transform: rotate(10deg);
  animation: rain-fall 0.55s linear infinite;
  opacity: 0.45;
  filter: blur(0.2px);
}
@keyframes rain-fall {
  from {
    transform: translateY(-10%) rotate(10deg);
  }
  to {
    transform: translateY(10%) rotate(10deg);
  }
}
.snow-layer {
  position: absolute;
  inset: -20%;
  background-image: radial-gradient(rgba(255,255,255,0.85) 1px, transparent 1.5px),
    radial-gradient(rgba(255,255,255,0.65) 1px, transparent 1.5px),
    radial-gradient(rgba(255,255,255,0.45) 1px, transparent 1.5px);
  background-size: 120px 120px, 180px 180px, 240px 240px;
  background-position: 0 0, 60px 40px, 120px 90px;
  animation: snow-fall 8s linear infinite;
  opacity: 0.55;
}
.bg-card {
  background: rgba(255,255,255,0.92);
  backdrop-filter: blur(8px);
}
@keyframes snow-fall {
  from {
    transform: translateY(-6%);
  }
  to {
    transform: translateY(6%);
  }
}
@media (prefers-reduced-motion: reduce) {
  .rain-layer, .snow-layer, .mc-sun, .mc-moon, .mc-sunrays, .mc-cloud, .mc-fog, .mc-heat {
    animation: none;
  }
}
